.sidebar {
   flex: 1;
   height:calc(100vh - 50px);
   overflow-y: hidden;
   position: sticky;
   top: 50px;
   background-color: rgb(245,245,245);
}
.sidebarWrapper {
   padding: 20px;
   color:#555;
}

.sidebarMenu {
   margin-bottom: 10px;
}

.sidebarTitle {
   font-size: 15px;
   font-weight: 700;
   color: rgb(197, 197, 197)
}

.sidebarList {
   list-style: none;
   padding: 5px;
   
}

.sidebarListItem {
   padding: 5px;
   cursor: pointer;
   display: flex;
   align-items: center;
   border-radius: 10px;
   transition: background-color 150ms linear;
}

.sidebarListItem.active, .sidebarListItem:hover {
   background-color: rgb(230,230,245);
}

.sidebarIcon {
   margin-right: 5px;
   font-size: 20px!important;
}

