.newUser {
  flex: 4;
  padding: 20px;

}


.newUserForm {
   display: flex;
   flex-wrap: wrap;
}

.newUserItem {
   width: 500px;
   display: flex;
   flex-direction: column;
   margin-top: 10px;
   margin-right: 20px;
}

.newUserItem > label {
   margin-bottom: 10px;
   font-size: 14px;
   font-weight: 600;
   color: rgb(150,150,150);
}

.newUserItem>input {
   height: 20px;
   padding: 10px;
   border: 1px solid lightgray;
   border-radius: 5px;
}

.newUserGender>input {
   margin-top: 15px;
}

.newUserGender>label {
   margin: 10px;
   font-size: 18px;
   color:#555;
}

.newUserSelect {
   height: 40px;
   border-radius: 5px;
   background-color: white;
}

.newUserButton {
   width: 200px;
   border:none;
   background-color: darkblue;
   color:white;
   padding: 7px 10px;
   font-weight: 600;
   font-size: 18px;
   border-radius: 10px;
   margin-top: 30px;
   cursor: pointer;
}